<template>
    <v-main class="v-main">
        <v-card class="login_form_card" elevation="5">
            <div class="login_form">
                <h1 style="margin-bottom:10%;">로그인</h1>
                <v-form ref="login_form">
                    <div class="input_field_div">
                        <v-text-field class="input_field email_input"
                            outlined
                            v-model="email_input"
                            type="email"
                            label="이메일"
                        ></v-text-field>
                    </div>
                    <div class="input_field_div">
                        <v-text-field class="input_field password_input"
                            outlined
                            v-model="password_input"
                            type="password"
                            label="비밀번호"    
                        ></v-text-field>
                    </div>
                    <!-- <v-hover>
                    <v-card
                        width="80px"
                        height="40px"
                    >
                        <v-card-text style="margin:auto;">로그인</v-card-text>
                    </v-card>
                    </v-hover> -->
                    <v-btn
                        @click="logInCall"
                        :disabled="login_button_disabled"
                        elevation="4"
                        type="submit"
                    >로그인</v-btn>
                    <div class="error--text" style="margin-top:3%;font-size:16px;font-weight:300;" v-if="login_error_raise.isRaised">{{login_error_raise.message}}</div>
                </v-form>
                <!-- <input type="email" v-model="email_input" placeholder="이메일을 입력하세요"><br>
                <input type="password" v-model="password_input" placeholder="비밀번호를 입력하세요"><br> -->
                <!-- <input type="button" @click="logInCall" :disabled="login_button_disabled" value="로그인"><br> -->
                <!-- <p v-if="login_error_raise.isRaised">{{login_error_raise.message}}</p> -->
                <p style="margin-top:30px;">계정이 존재하지 않는다면 <router-link :to="{name:'sign_up'}">회원가입 하기</router-link></p>
            </div>
        </v-card>
    </v-main>
</template>
<script>
// import {extend, ValidationObserver, ValidationProvider} from 'vee-validation'
export default{
    data() {
        return {
            email_input: '',
            password_input: '',

            login_button_disabled: false,

            login_error_raise: {
                isRaised: false,
                message: ''
            },
        }
    },
    methods: {
        logInCall: function() {
            this.login_button_disabled = true;

            this.$store.dispatch('logIn', {
                email: this.email_input,
                password: this.password_input
            }).then((data) => {
                console.log(`result of data : ${data}`);
                if(data != true) {
                    this.login_error_raise.isRaised = true;
                    switch(data) {
                        case 'auth/invalid-email':
                            this.login_error_raise.message = '올바르지 않은 이메일 주소 입니다.'
                            break;
                        default:
                            this.login_error_raise.message = '존재하지 않는 이메일 주소 또는 틀린 비밀번호 입니다.'
                            break;
                    }   
                } else {
                    this.$router.push({name:'home'})
                }
                this.login_button_disabled = false;
            }).catch((error) => {
                console.error(error);
            });
        },
        validation: function() {

        }
    },
    created() {
        if (this.$store.state.isLogin) {
            this.$router.push({name:'home'})
        }
    }
}
</script>
