import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../components/home.vue'
import login from '../components/login.vue'
import sign_up from '../components/sign_up.vue'
import schedule from '../components/schedule.vue'
import meal_diet from '../components/meal_diet.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/sign_up',
    name: 'sign_up',
    component: sign_up
  },
  {
    path: '/schedule',
    name: 'schedule',
    component: schedule
  },
  {
    path: '/meal_diet',
    name: 'meal_diet',
    component: meal_diet
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
