function getWeek() {
  var day = new Date().getDay();
  if(day == 0) {
    day = 1;
  } else if(day == 6) {
    day = 5;
  }
  return day;
}

function makeRandomZoomCode(l) {
  var r = '';
  var c = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  for(var i = 0;i < l;i++) {
    r += c.charAt(Math.floor(Math.random() * c.length));
  }
  return r
}

