    getSchedule(state) {
      var date = Date.now()
      var weekday = new Date().getDay()
      var mon = new Date(date - ((weekday - 1) * 1000 * 60 * 60 * 24))
      var fri = new Date(date - ((weekday - 5) * 1000 * 60 * 60 * 24))
      var schedule_table = new Array()
      var TI_FROM_YMD = String(mon.getFullYear()) + String(mon.getMonth() + 1).padStart(2, '0') + String(mon.getDate()).padStart(2, '0')
      var TI_TO_YMD = String(fri.getFullYear()) + String(fri.getMonth() + 1).padStart(2, '0') + String(fri.getDate()).padStart(2, '0')
      var ATPT_OFCDC_SC_CODE = state.state.userInfo.ATPT_OFCDC_SC_CODE
      var SD_SCHUL_CODE = state.state.userInfo.SD_SCHUL_CODE
      var GRADE = state.state.userInfo.GRADE
      var CLASS_NM = state.state.userInfo.CLASS_NM
      var url = 'https://open.neis.go.kr/hub/misTimetable?key=13eca7ab97a346ebaf5575f3e82b043d&Type=json&ATPT_OFCDC_SC_CODE=' + ATPT_OFCDC_SC_CODE + '&SD_SCHUL_CODE=' + SD_SCHUL_CODE + '&GRADE=' + GRADE + '&CLASS_NM=' + CLASS_NM + '&TI_FROM_YMD=' + TI_FROM_YMD + '&TI_TO_YMD=' + TI_TO_YMD
      // console.log(url);
      var result = null
      $.ajax({
        url: url,
        type: 'get',
        dataType: 'json',
        async: false,
        success: function (data) {
          result = data['misTimetable'][1]['row']
          // console.error('api 호출에 실패하였습니다.');
        }
      })
      var schedule_date = result.map(x => x['ALL_TI_YMD'])
      var schedule_class_length_list = [0, 0, 0, 0, 0] // 요일 별 교시 개수
      var schedule_name_list = new Array()
      var buffer = schedule_date[0]
      var n = 0
      for (var i = 0; i < schedule_date.length; i++) {
        if (schedule_date[i] != buffer) {
          n++
          buffer = schedule_date[i]
        }
        schedule_class_length_list[n]++
      }
      i = 0
      for (var j = 0; j < 5; j++) {
        schedule_name_list.push(new Array())
        for (var k = 0; k < schedule_class_length_list[j]; k++) {
          var sn = ''
          if (result[i].ITRT_CNTNT.replace('-', '') == '기술·가정') {
            sn = '기가'
          } else if (result[i].ITRT_CNTNT.replace('-', '') == '생활 중국어') {
            sn = '중국어'
          } else if (result[i].ITRT_CNTNT.replace('-', '') == '봉사활동') {
            sn = '봉사'
          } else if (result[i].ITRT_CNTNT.replace('-', '') == '성교육자율활동') {
            sn = '성교육'
          } else if (result[i].ITRT_CNTNT.replace('-', '') == '동아리활동') {
            sn = '동아리'
          } else if (result[i].ITRT_CNTNT.replace('-', '') == '진로활동') {
            sn = '진로'
          } else if (result[i].ITRT_CNTNT.replace('-', '') == '자율활동') {
            sn = '자율'
          } else {
            sn = result[i].ITRT_CNTNT.replace('-', '')
          }
          schedule_name_list[j].push(sn)
          i++
        }
      }
      for (i = 0; i < 5; i++) {
        while (schedule_name_list[i].length != Math.max(...schedule_class_length_list)) {
          schedule_name_list[i].push('')
        }
      }

      schedule_table = new Array()
      for (i = 0; i < 6; i++) {
        schedule_table.push(new Array())
      }
      schedule_table[0][0] = {
        class: ['class_info', 'day_info'],
        value: '교시'
      }
      var weekdays = ['월', '화', '수', '목', '금']
      for (i = 0; i < 5; i++) {
        schedule_table[i + 1].push({
          class: ['day_info'],
          value: weekdays[i]
        })
      }
      for (i = 0; i < Math.max(...schedule_class_length_list); i++) {
        schedule_table[0].push({
          class: ['class_info'],
          value: i + 1
        })
        for (j = 0; j < 5; j++) {
          if (schedule_name_list[j][i] == '') {
            schedule_table[j + 1].push({
              class: ['null_subject'],
              value: '　'
            })
          } else {
            schedule_table[j + 1].push({
              class: [schedule_name_list[j][i]],
              value: schedule_name_list[j][i]
            })
          }
        }
      }
      // console.log('schedule_table',schedule_table);
      return schedule_table
    },
    getMealDiet(state, loadDate) {
      var date = Date.now()
      var weekday = new Date().getDay()
      var week = (state.state.meal_diet.index[loadDate + 1] + 1) * loadDate
      // console.log('week!!!!!',week)
      // var week = loadDate;
      var mon = new Date(date - ((weekday - 1 + (week * -7)) * 1000 * 60 * 60 * 24))
      var fri = new Date(date - ((weekday - 5 + (week * -7)) * 1000 * 60 * 60 * 24))
      var meal_diet = new Array()
      var MLSV_FROM_YMD = String(mon.getFullYear()) + String(mon.getMonth() + 1).padStart(2, '0') + String(mon.getDate()).padStart(2, '0')
      var MLSV_TO_YMD = String(fri.getFullYear()) + String(fri.getMonth() + 1).padStart(2, '0') + String(fri.getDate()).padStart(2, '0')
      var ATPT_OFCDC_SC_CODE = state.state.userInfo.ATPT_OFCDC_SC_CODE
      var SD_SCHUL_CODE = state.state.userInfo.SD_SCHUL_CODE
      var url = 'https://open.neis.go.kr/hub/mealServiceDietInfo?key=13eca7ab97a346ebaf5575f3e82b043d&Type=json&ATPT_OFCDC_SC_CODE=' + ATPT_OFCDC_SC_CODE + '&SD_SCHUL_CODE=' + SD_SCHUL_CODE + '&MLSV_FROM_YMD=' + MLSV_FROM_YMD + '&MLSV_TO_YMD=' + MLSV_TO_YMD
      // console.log(url);
      var result = null
      // var _result = new Object();
      $.ajax({
        url: url,
        type: 'get',
        dataType: 'json',
        async: false,
        success: function (data) {
          result = data['mealServiceDietInfo'][1]['row']
        }
      })
      if (loadDate == 0) {
        _date = getWeek()
      }
      var _date
      // console.error(_date)
      var _datas
      var _index = state.state.meal_diet.index
      if (loadDate == 1) {
        _date = state.state.meal_diet.date
        _datas = state.state.meal_diet.datas.concat(result)
        _index[2]++
      } else if (loadDate == -1) {
        _date = ((state.state.meal_diet.index[0] + state.state.meal_diet.index[2] + 1) * 5) + getWeek()
        _datas = result.concat(state.state.meal_diet.datas)
        _index[0]++
      } else {
        _datas = result
      }
      // console.log(state.state.meal_diet);
      return {
        date: _date,
        datas: _datas,
        index: _index
      }
    },
