<template>
    <v-app>
    <frame name="zoom_frame"></frame>
    <v-card elevation="11" style="margin:auto;padding:20px;margin-top:40px;">
    <tbody class="schedule_tbody">
        <td v-for="(a, i) in schedule_table" :key="i">
            <tr v-for="(b,j) in a" :key="j" :class="getClassStr(b.class,'')">
                <v-hover v-slot="{ hover }">
                <v-card style="margin:8px;padding:17px;display:flex;justify-content:center;" :class="`elevation-${hover ? 5 : 3}`" class="transition-swing">
                <a :class="getClassStr(b.class)" style="text-decoration: none;color:black;">{{b.value}}</a>
                </v-card></v-hover>
            </tr>
        </td>
    </tbody>
    </v-card>
    <p style="height:0;width:0;font-size:0">{{zoom_id_list}}</p>
    <p style="height:0;width:0;font-size:0;">{{zoom_id_list_array}}</p>
    <input type="button" value="줌 아이디 새로고침하기" @click="this.$store.dispatch('refreshZoomId')">
    <input type="button" value="줌 아이디 공유 코드 만들기" @click="this.$store.dispatch('makeZoomCode')">
    <!-- <input type="button" value="줌 아이디 업로드하기" @click="updateZoomId"> -->
    <input type="button" value="수정하기" @click="zoom_id_setter++">
    <input type="button" value="+" @click="zoomIdListArrayAddShow()" v-if="zoom_id_setter%2 != 0">
    <input type="button"  value="줌 아이디 공유받기" @click="sharedZoomIdShow()">
    <div v-if="zoom_id_shared">
        <input type="text" v-model="zoom_id_shared_code_input">
        <input type="button" value="공유받기" @click="sharedZoomId()">
    </div>
    <!-- <input type="button" value="make shatre code" @click="makeZoomCode">
    <input type="button" value="updata zoom id" @click="updataZoomId">
    <input type="button" @click="this.$store.dispatch('refreshZoomId')"> -->
    <!-- <div v-for="i in data_length" :key="i"><input type="text" placeholder="subject" v-model="data[0][i]"><input type="text" placeholder="number" v-model="data[1][i]"></div> -->
    <div v-if="zoom_id_setter%2 != 0">
        <div v-for="(a, i) in zoom_id_list_array" :key="i">
            <div style="display: flex;">
            <p>{{zoom_id_list_array[i][0]}}</p> : 
            <p v-if="!zoom_id_list_array_edit[i]">{{zoom_id_list_array[i][1]}}</p>
            <input type="text" :placeholder="zoom_id_list_array[i][1]" v-model="zoom_id_list_array_edit_text[i]" v-if="zoom_id_list_array_edit[i]">
            <input type="button" value="수정" @click="zoomIdListArrayEdit(i)">
            <input type="button" value="삭제" @click="zoomIdListArrayDelete(i)" v-if="zoom_id_list_array_delete_show">
        </div>
            <!-- <input type="text" :placeholder="zoom_id_list_array[i][0]">
            <input type="text" :placeholder="zoom_id_list_array[i][1]"> -->
        </div>
        <div v-if="zoom_id_list_array_add">
            <input type="text" placeholder="과목" v-model="zoom_id_list_array_add_sub">
            <input type="text" placeholder="번호" v-model="zoom_id_list_array_add_num">
            <input type="button" value="완료" @click="zoomIdListArrayAdd()">
        </div>
    </div>
    </v-app>
</template>
<script>
import $ from 'jquery'
export default({
    data() {
        return {
            zoom_id_setter: 0,
            zoom_id_list_array: new Array(),
            zoom_id_list_array_input: new Array(),
            zoom_id_list_array_edit: new Array(),
            zoom_id_list_array_edit_text: new Array(),
            zoom_id_list_array_add: false,
            zoom_id_list_array_add_sub: '',
            zoom_id_list_array_add_num: '',
            zoom_id_shared: false,
            zoom_id_shared_code_input: '',
            zoom_id_list_array_delete_show: true,
        }  
    },
    methods: {
        getClassStr: function(arr){
            var n = '';
            if(arguments.length == 2){
                n = '_tr';
            }
            return arr.join(n+' ')+n;
        },
        add: function() {
            this.zoom_id_list_array_input.push([[],[]])
        },
        makeZoomCode: function() {
            this.$store.dispatch('makeZoomCode');
        },
        updateZoomId: function() {
            this.$store.dispatch('updateZoomId');
        },
        objToArray: function(obj) {
            return JSON.stringify(obj).replace(/"|{|}/g,'').split(',').map(a => a.split(':'))
        },
        arrayToObj: function(arr) {
            // console.log(arr, JSON.parse(`{${arr.map(a => a.map(b => `'${b}'`).join(':'))}`));
            // return JSON.parse(`{${arr.map(a => a.map(b => `'${b}'`).join(':'))}`);
            return JSON.parse(`{${arr.map(a=>a.map(b=>`"${b}"`).join(':'))}}`);
        },
        zoomIdListArrayRefresh: function() {
            this.zoom_id_list_array = this.objToArray(this.$store.state.zoom_id_list);
            for(var j = 0;j<this.zoom_id_list_array.length;j++) {
                this.zoom_id_list_array_input.push([[],[]]);
                this.zoom_id_list_array_edit.push(0);
                this.zoom_id_list_array_edit_text.push('');
            }
            var z_l = this.zoom_id_list_array
            console.log(z_l);
            if(z_l[0] != '') {
                for(var i = 0;i<z_l.length;i++) {
                    $('.'+z_l[i][0]).attr('href', 'https://zoom.us/j/'+z_l[i][1]).attr('target', 'zoom_frame');
                }
            }
        },
        zoomIdListArrayEdit: function(i) {
            if(this.zoom_id_list_array_edit[i]) {
                this.zoom_id_list_array[i][1] = this.zoom_id_list_array_edit_text[i];
                this.$store.commit('changeZoomId',this.arrayToObj(this.zoom_id_list_array));
                this.updateZoomId()
                this.zoom_id_list_array_edit[i] = false;
            } else {
                this.zoom_id_list_array_edit[i] = true
            }
        },
        zoomIdListArrayDelete: function(i) {
            this.zoom_id_list_array.splice(i,1);
            this.$store.commit('changeZoomId',this.arrayToObj(this.zoom_id_list_array));
            this.updateZoomId()
        },
        zoomIdListArrayAdd: function() {
            this.zoom_id_list_array.push([[this.zoom_id_list_array_add_sub],[this.zoom_id_list_array_add_num]]);
            this.$store.commit('changeZoomId',this.arrayToObj(this.zoom_id_list_array));
            this.updateZoomId()
            this.zoom_id_list_array_add_sub = '';
            this.zoom_id_list_array_add_num = '';
            this.zoom_id_list_array_add = false;
        },
        zoomIdListArrayAddShow: function() {
            if(this.zoom_id_list_array_add) {
                this.zoom_id_list_array_add_sub = '';
                this.zoom_id_list_array_add_num = '';         
                this.zoom_id_list_array_add = false;
            } else {
                this.zoom_id_list_array_add = true;
            }
        },
        makeZoomIdShareCode: function() {
            console.log(this.$store.state.userInfo.zoom_id_list.sharing_zoom_id_code);
        },
        sharedZoomId: function() {
            this.$store.dispatch('getZoomId',this.zoom_id_shared_code_input)
        },
        sharedZoomIdShow: function() {
            if(this.zoom_id_shared) {
                this.zoom_id_shared = false;
            } else {
                this.zoom_id_shared = true;
            }
        },
    },
    computed: {
        schedule_table() {
            if(this.$store.state.isLogin) {
                return this.$store.state.schedule_table;
            } else {
                return null;
            }
        },
        zoom_id_list() {
            this.zoomIdListArrayRefresh()
            return this.$store.state.zoom_id_list
        },
        // zoom_id_list_array() {
        //     $('.역사').attr('href','http://google.com')  
        //     return this.objToArray(this.$store.state.zoom_id_list)
        // }
    },
    created() {
        if (!this.$store.state.isLogin) {
            this.$router.push({name:'home'})
        }
    },
    watch: {
        zoom_id_list_array: {
            deep: true,
            handler: function(data) {
                if(data.length == 1) {
                    this.zoom_id_list_array_delete_show =  false
                } else {
                    this.zoom_id_list_array_delete_show =  true;
                }
            }
        }
        // zoom_id_list: {
        //     // zoom_id_list_array = objToArray($store.state.zoom_id_list)
        //     deep: true,
        //     handler: function(new_) {
        //         console.log("kkk")
        //         this.zoom_id_list_array = this.objToArray(new_)
        //         console.log(this.zoom_id_list_array)
        //     }
        // }
    },
    mounted() {
        console.log(this.schedule_table);
        this.$store.dispatch('refreshZoomId');
        // $(".과학").attr("href", "https://zoom.us/j/121111111").attr("target", "zoom_frame");
        // $(".수학").attr("href", "https://zoom.us/j/9387780425").attr("target", "zoom_frame");
        // console.log(this.objToArray(this.zoom_id_list));
    },
})
</script>
<style>
</style>
