import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import {initializeApp} from '@firebase/app'
import {getAuth, onAuthStateChanged} from '@firebase/auth'
import {getDatabase, ref, get} from '@firebase/database'

const firebaseConfig = {
  apiKey: "AIzaSyAaosy2Ia_Syh8Y7n-otxQkFhmtxTmBA0Y",
  authDomain: "online-class-helper-329604.firebaseapp.com",
  databaseURL: "https://online-class-helper-329604-default-rtdb.firebaseio.com",
  projectId: "online-class-helper-329604",
  storageBucket: "online-class-helper-329604.appspot.com",
  messagingSenderId: "766539149658",
  appId: "1:766539149658:web:0d319d0ab8220cbd1b4363"
};
const firebaseApp = initializeApp(firebaseConfig);
const firebaseAuth = getAuth(firebaseApp);
const firebaseDatabase = getDatabase(firebaseApp);
var _first = true;

Vue.config.productionTip = false
onAuthStateChanged(firebaseAuth, (user) => {
  if(user) {
    get(ref(firebaseDatabase, 'users/' + user.uid))
      .then((snapshot) => {
        store.commit('changeUserInfo', {
          isLogin: true,
          user: user,
          userInfo: snapshot.val()                    
        })
          store.dispatch('getSchedule')
            .then((data) => {
              store.commit('changeSchedule', data);
              store.dispatch('getMealDiet', 0)
                .then((_data) => {
                  store.commit('changeMealDiet', _data);
                  console.log('gzi',store.state.userInfo.zoom_id_list.list)
                  store.commit('changeZoomId', store.state.userInfo.zoom_id_list.list)
                  if(_first) {
                    new Vue({
                      router,
                      store,
                      vuetify,
                      render: h => h(App)
                    }).$mount('#app')
                    _first = false;
                  }                              
                })
              })
            })
  } else {
    store.commit('changeUserInfo', {
      isLogin: false,
      user: null,
      userInfo: null
    });
    store.commit('changeMealDiet', {
      date: null,
      datas: null,
      index: null
    });
    if(_first) {
      new Vue({
        router,
        store,
        vuetify,
        render: h => h(App)
      }).$mount('#app')
      _first = false;
    }
  }
})
