<template>
    <v-main>
        <input type="button" value="logout" @click="logout">
        <p>{{welcome}}</p>
        <router-link :to="{name:'login'}">로그인 하기</router-link>
        <router-link :to="{name:'sign_up'}">회원가입 하기</router-link>
    </v-main>
</template>
<script>
export default({
    data() {
        return {
            userInfo: null,
        }
    },
    methods: {
        logout: function() {
            this.$store.dispatch('signOut');
        },
    },
    watch: {
    },
    computed: {
        welcome() {
            if(this.$store.state.isLogin) {
                return this.$store.state.userInfo.user_name + '님 환영합니다';
            } else {
                return '현재 로그인 되어있는 상태가 아닙니다.'
            }
        }
    },
    created() {
    },
})
</script>
