<template>
<v-main>
    <v-card class="sign_up_form_card" elevation="5">
        <div class="sign_up_form">
            <h1>회원가입</h1>
            <v-form ref="sign_up_form">
                <div class="input_field_div">
                    <v-text-field class="input_field"
                        outlined
                        v-model="email_input"
                        type="email"
                        label="이메일"
                    ></v-text-field>
                </div>
                <div class="input_field_div">
                    <v-text-field class="input_field"
                        outlined
                        v-model="password_input"
                        type="password"
                        label="비밀번호"
                    ></v-text-field>
                </div>
                <div class="input_field_div">
                    <v-text-field class="input_field"
                        outlined
                        v-model="password_re_input"
                        type="password"
                        label="비밀번호 재입력"
                    ></v-text-field>
                </div>
                <div class="input_field_div">
                    <v-text-field class="input_field"
                        outlined
                        v-model="name_input"
                        type="text"
                        label="이름"
                    ></v-text-field>
                </div>
                <div class="input_field_div" style="display:flex;">
                    <v-text-field class="input_field"
                        outlined
                        v-model="school_name_input"
                        type="text"
                        label="학교 이름"
                    ></v-text-field>
                    <v-btn @click="getSchoolList">학교 검색</v-btn>
                </div>
                <!-- <div class="input_field_div">
                    <v-select
                        class="input_field"
                        v-model="selected_school"
                        :items="school_list"
                        :item-text="item => item.ORG_RDNMA + ' ' + item.SCHUL_NM"
                        return-object
                        outlined
                    ></v-select>
                </div> -->
                <div class="input_field_div">
                    <v-select
                        class="input_field"
                        placeholder="학교를 선택해주세요."
                        v-model="selected_school"
                        :items="school_list"
                        :item-text="item => item.ORG_RDNMA + ' ' + item.SCHUL_NM"
                        no-data-text="학교를 선택해주세요."
                        return-object
                        outlined
                    ></v-select>
                </div>
                <div class="input_field_div">
                    <v-select
                        class="input_field"
                        placeholder="학년을 선택해주세요."
                        v-model="selected_grade"
                        :items="grade_list"
                        no-data-text="학년을 선택해주세요."
                        outlined
                    ></v-select>
                </div>
                <div class="input_field_div">
                    <v-select
                        class="input_field"
                        placeholder="반을 선택해주세요"
                        v-model="selected_class"
                        :items="selected_class_list"
                        no-data-text="반을 선택해주세요."
                        outlined
                    ></v-select>
                </div>
                <v-btn
                    @click="signUp"
                        
                >회원가입</v-btn>
                <div class="error--text" style="margin-top:3%;font-size:16px;font-weight:300;" v-if="sign_up_error_raise.isRaised">{{sign_up_error_raise.message}}</div>
            </v-form>
            <p style="margin-top:30px;">이미 계정이 있다면 <router-link :to="{name:'login'}">로그인 하기</router-link></p>
        </div>
        <!-- <input type="email" v-model="email_input" placeholder="이메일을 입력하세요"><br>
        <input type="password" v-model="password_input" placeholder="비밀번호를 입력하세요"><br>
        <input type="password" v-model="password_re_input" placeholder="비밀번호를 다시 입력하세요"><br>
        <input type="text" v-model="name_input" placeholder="이름을 입력하세요"><br>
        <input type="text" v-model="school_name_input" placeholder="학교 이름을 입력하세요">
        <input type="button" @click="getSchoolList" value="학교 검색"><br> -->
        <!-- {{school_list}} -->
        
        
        
        
        <!-- <select name="school_select" v-model="selected_school" v-if="school_select_render">
            <option value="" disabled selected>학교를 선택해주세요.</option>
            <option v-for="(a, i) in school_list" :key="i" :value="a">{{a.ORG_RDNMA + ' ' + a.SCHUL_NM}}</option>
        </select><br>
        <select name="grade_select" v-model="selected_grade" v-if="grade_and_class_select_render">
            <option value="" disabled selected>학년을 선택해주세요.</option>
            <option v-for="(a, i) in grade_list" :key="i" :value="a">{{a}}학년</option>
        </select><br>
        <select name="class_select" v-model="selected_class" v-if="grade_and_class_select_render">
            <option value="" disabled selected>반을 선택해주세요.</option>
            <option v-for="(a, i) in selected_class_list" :key="i" :value="a">{{a}}반</option>
        </select><br>

        <input type="button" @click="signUp" value="회원가입하기">
        <p>이미 계정이 있다면 <router-link :to="{name:'login'}">로그인 하기</router-link></p> -->
        
        </v-card>
    </v-main>
</template>
<script>
import $ from 'jquery'
export default {
    data(){
        return {
            email_input: '',
            password_input: '',
            password_re_input: '',
            name_input: '',
            school_name_input: '',

            school_list: new Array(),
            grade_list: new Array(),
            class_list: new Array(),
            selected_class_list: new Array(),

            selected_school: '',
            selected_grade: '',
            selected_class: '',
            
            school_select_render: true,
            grade_and_class_select_render: true,

            sign_up_button_disabled: false,
            sign_up_error_raise: {
                isRaised: false,
                message: ''
            }
        }
    },
    methods:{
        getSchoolList: function() {
            if(this.school_name_input.length > 0) {
                this.$store.dispatch('getSchoolList', this.school_name_input)
                .then((data) => {
                    this.school_list = data;
                }).catch((error) => {
                    console.error(error);
                });
            }
        },
        getGradeAndClassInfo: function() {
            this.$store.dispatch('getGradeAndClassInfo', {
                ATPT_OFCDC_SC_CODE: this.selected_school.ATPT_OFCDC_SC_CODE,
                SD_SCHUL_CODE: this.selected_school.SD_SCHUL_CODE
            }).then((data) => {
                this.grade_list = data.grade_list;
                this.class_list = data.class_list;
                this.grade_and_class_select_render = false;
                this.grade_and_class_select_render = true;
            }).catch((error) => {
                console.error(error);
            });
        },
        signUp: function(){
            this.sign_up_button_disabled = true;
            if(this.password_input != this.password_re_input) {
                this.sign_up_error_raise.isRaised = true;
                this.sign_up_error_raise.message = '비밀번호 재 입력란이 일치하지 않습니다.'
            } else if(this.selected_class == '') {
                this.sign_up_error_raise.isRaised = true;
                this.sign_up_error_raise.message = '모든 칸을 채워주세요.'
            } else if(this.name_input.length < 2) {
                this.sign_up_error_raise.isRaised = true;
                this.sign_up_error_raise.message = '이름은 두 글자 이상이어야 합니다.'
            } else if(this.password_input.length < 8) {
                this.sign_up_error_raise.isRaised = true;
                this.sign_up_error_raise.message = '비밀번호는 여덟 글자 이상이어야 합니다.'
            } else {
                this.$store.dispatch('signUp', {
                email: this.email_input,
                password: this.password_input,
                user_name: this.name_input,
                SCHUL_NM: this.selected_school.SCHUL_NM,
                ATPT_OFCDC_SC_CODE: this.selected_school.ATPT_OFCDC_SC_CODE,
                SD_SCHUL_CODE: this.selected_school.SD_SCHUL_CODE,
                GRADE: this.selected_grade,
                CLASS_NM: this.selected_class
                })
                    .then((data) => {
                        if(data != true) {
                            this.sign_up_error_raise.isRaised = true;
                            switch(data) {
                                case 'auth/invalid-email':
                                    this.sign_up_error_raise.message = '올바르지 않은 이메일 형식입니다.'
                                    break;
                                default:
                                    this.sign_up_error_raise.message = '다시 시도해 주세요.'
                                    break;
                            }
                        } else {
                            this.$router.push({name:'home'});
                        }
                    })
            }
            this.sign_up_button_disabled = false;
        },
    },
    watch: {
        selected_school : function() {
            this.getGradeAndClassInfo();
        },
        selected_grade : function() {
            this.selected_class_list = this.class_list[this.grade_list.indexOf(this.selected_grade)];
            this.grade_and_class_select_render = false;
            this.grade_and_class_select_render = true;
        },
    },
    created() {
        if (this.$store.state.isLogin) {
            this.$router.push({name:'home'})
        }
    }
}
</script>